"use client";
import { createClient } from "@supabase/supabase-js";
import { useEffect, useState } from "react";

// Initialize Supabase client - replace with your actual URL and anon key
const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL || "",
  process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY || ""
);

const Categorias = () => {
  const [openDropdown, setOpenDropdown] = useState<string | null>(null);
  const [categories, setCategories] = useState<
    Array<{
      name: string;
      id: number;
      items: string[];
    }>
  >([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchCategoriesAndProducts() {
      try {
        // Fetch categories from Supabase
        const { data: categoriesData, error: categoriesError } = await supabase
          .from("categorias")
          .select("id_categoria, nombre_categoria");

        if (categoriesError) throw categoriesError;

        // Create array to hold our categories with products
        const categoriesWithProducts = [];

        // For each category, fetch related products
        for (const category of categoriesData) {
          const { data: productsData, error: productsError } = await supabase
            .from("productos")
            .select("nombre_producto")
            .eq("id_categoria", category.id_categoria);

          if (productsError) throw productsError;

          // Add to our categories array
          categoriesWithProducts.push({
            id: category.id_categoria,
            name: category.nombre_categoria,
            items: productsData.map((product) => product.nombre_producto),
          });
        }

        setCategories(categoriesWithProducts);
      } catch (error) {
        console.error("Error fetching data:", error);
      } finally {
        setLoading(false);
      }
    }

    fetchCategoriesAndProducts();
  }, []);

  const handleDropdownToggle = (category: string) => {
    if (openDropdown === category) {
      setOpenDropdown(null);
    } else {
      setOpenDropdown(category);
    }
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center py-4">
        Cargando categorías...
      </div>
    );
  }

  return (
    <div className="flex justify-center items-center gap-15 text-lg pt-4 pb-4">
      <a
        href="#mejores-ofertas"
        className="cursor-pointer hover:text-red-600 transition-colors mx-4"
      >
        Ofertas
      </a>
      {categories.map((category) => (
        <div key={category.id} className="relative mx-4">
          <button
            onClick={() => handleDropdownToggle(category.name)}
            className="flex items-center hover:text-red-600 transition-colors"
          >
            {category.name}
            <svg
              className={`ml-1 w-4 h-4 transition-transform ${
                openDropdown === category.name ? "rotate-180" : ""
              }`}
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M19 9l-7 7-7-7"
              ></path>
            </svg>
          </button>
          {openDropdown === category.name && (
            <div className="absolute z-50 mt-2 w-64 bg-white rounded-md shadow-lg py-1 text-base max-h-96 overflow-y-auto left-0">
              {category.items.length > 0 ? (
                category.items.map((item) => (
                  <a
                    key={item}
                    href={`#${item.toLowerCase().replace(/\s+/g, "-")}`}
                    className="block px-4 py-2 text-gray-800 hover:bg-gray-100"
                  >
                    {item}
                  </a>
                ))
              ) : (
                <span className="block px-4 py-2 text-gray-500">
                  No hay productos en esta categoría
                </span>
              )}
            </div>
          )}
        </div>
      ))}
    </div>
  );
};

export default Categorias;
